export const getTempScalesState = ({ scales }) => scales;

export const setTempScalesState = (scales) => (state) => ({ ...state, scales });

export const getTempScalesList = (state) => getTempScalesState(state).scalesList;

export const getActiveTempScale = (state) => getTempScalesState(state).activeTempScale;

export const setActiveTempScale = (activeTempScale) => (state) => setTempScalesState({
  ...getTempScalesState(state), activeTempScale,
})(state);
