import API_KEYS from '../api-keys';

export default async function fetchBackgroundImage(geoData, currentWeather) {
  const { unsplash } = API_KEYS;
  const weatherDescription = currentWeather.currently ? currentWeather.currently.summary : 'nature';
  const weatherDescriptionTag = weatherDescription.split(' ').join('%20');

  const searchUrl = `https://api.unsplash.com/photos/random?query=${geoData.season}+${geoData.dayTime}+${weatherDescriptionTag}&client_id=${unsplash}`;

  const responseData = await fetch(searchUrl).then((response) => response.json());

  const urlString = responseData.urls.regular;

  const backgroundElement = document.querySelector('#root');


  backgroundElement.style.backgroundImage = `url(${urlString})`;

  return urlString;
}
