import createDomElement from '../../../utils/dom/createDomElement';

import './input-form.css';

export default function renderInputForm({
  classNames = [],
  onSubmit,
  caption,
}) {
  const form = createDomElement('form', classNames.concat('form'));

  const textInput = createDomElement('input', classNames.concat('text-input'));

  textInput.type = 'text';

  const buttonInput = createDomElement('input', classNames.concat('button-input'));

  buttonInput.type = 'submit';

  buttonInput.value = caption;

  form.appendChild(textInput);
  form.appendChild(buttonInput);

  if (onSubmit) {
    form.addEventListener('submit', (e) => {
      e.preventDefault();
      onSubmit(e);
    });
  }

  return form;
}
