const TIMES_OF_DAY = {
  night: [0, 4],
  morning: [4, 12],
  day: [12, 17],
};

const getTimeOfDay = (date) => {
  const hours = date.getHours();
  return Object.keys(TIMES_OF_DAY).find((key) => {
    const [start, end] = TIMES_OF_DAY[key];
    return hours >= start && hours < end;
  }) || 'evening';
};

export default getTimeOfDay;
