import API_KEYS from '../api-keys';

export default async function loadWeather(geoData, lang, tempScale) {
  const { darkSky } = API_KEYS;
  const requestLink = `https://cors-anywhere.herokuapp.com/https://api.darksky.net/forecast/${darkSky}/${geoData.lat},${geoData.lng}?exclude=hourly&units=${tempScale.code}&lang=${lang}`;

  const responseData = await (await fetch(requestLink)).json();

  return responseData;
}
