import UpdateListener from '../../common/models/update-listener';

import { setGeoDataObject, setGeoDataLoadingState, getGeoDataObject } from '../state/geodata';
import { getActiveDictionary } from '../state/languages';


export default class GeoDataLoader extends UpdateListener {
  constructor({ loadGeoData, ...restProps }) {
    super(restProps);

    this.loadGeoData = loadGeoData;
  }

  // eslint-disable-next-line class-methods-use-this
  extractPropsFromState(state) {
    return {
      currentGeoData: getGeoDataObject(state),
      currentLang: getActiveDictionary(state),
    };
  }

  onStateUpdate({ currentGeoData: prevCurrentGeoData, currentLang: prevCurrentLang }) {
    const { currentGeoData, currentLang } = this.props;

    if ((!prevCurrentGeoData) || (currentGeoData.cityName !== prevCurrentGeoData.cityName)
        || currentLang['locale.name'] !== prevCurrentLang['locale.name']) {
      this.updateState(setGeoDataLoadingState(true));

      this.loadGeoData(currentGeoData.cityName, currentLang['locale.name'])
        .then((geoDataObject) => this.updateState(setGeoDataObject(geoDataObject)))
        .finally(() => this.updateState(setGeoDataLoadingState(false)));
    }
  }
}
