/* eslint-disable no-undef */
import renderButton from '../../../common/components/button';

import Element from '../../../common/models/element';

import { getActiveDictionary } from '../../state/languages';
import { getGeoDataObject, setGeoDataObject } from '../../state/geodata';

export default class SpeechRecognitionButton extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.recognition = new webkitSpeechRecognition();
    this.speechRecognitionList = new webkitSpeechGrammarList();
    this.handleRecognition = this.handleRecognition.bind(this);
  }

  extractPropsFromState(state) {
    return {
      activeDictionary: getActiveDictionary(state),
      currentGeoDataObject: getGeoDataObject(state),
    };
  }

  handleRecognition() {
    const grammar = '#JSGF V1.0; public <city>;';

    this.speechRecognitionList.addFromString(grammar, 1);
    this.recognition.grammars = this.speechRecognitionList;
    this.recognition.lang = 'en-US';
    this.recognition.interimResults = false;
    this.recognition.maxAlternatives = 1;

    const { currentGeoDataObject } = this.props;
    const geoDataCopy = { ...currentGeoDataObject };

    this.recognition.onresult = (e) => {
      const last = e.results.length - 1;
      geoDataCopy.cityName = e.results[last][0].transcript;
      if (geoDataCopy.cityName) {
        this.updateState(setGeoDataObject(geoDataCopy));
      }
    };

    this.recognition.onspeechend = () => {
      this.recognition.stop();
    };

    this.recognition.start();
  }


  render() {
    const { activeDictionary } = this.props;
    const buttonText = activeDictionary['recognition.buttonText'];
    const recognitionButton = renderButton({ caption: buttonText, onClick: this.handleRecognition, classNames: ['recognition'] });

    return recognitionButton;
  }
}
