import createDomElement from '../../../utils/dom/createDomElement';
import renderListItem from './renderListItem';

import './list.css';

export default function renderList({
  classNames = [],
  itemClassNames = [],
  items = [],
  listItemRenderer = renderListItem,
}) {
  const root = createDomElement('ul', classNames.concat('list'));

  items.forEach((item) => root.appendChild(listItemRenderer({ item, classNames: itemClassNames.concat('list__item') })));

  return root;
}
