export const getDictionariesState = ({ dictionaries }) => dictionaries;

export const setDictionariesState = (dictionaries) => (state) => ({ ...state, dictionaries });

export const getDictionariesList = (state) => getDictionariesState(state).dictionariesList;

export const getActiveDictionary = (state) => getDictionariesState(state).activeDictionary;

export const setActiveDictionary = (activeDictionary) => (state) => setDictionariesState({
  ...getDictionariesState(state), activeDictionary,
})(state);
