import UpdateListener from '../update-listener';

const HIDDEN_PROPS_KEY = Symbol('hidden-props-key');

export default class Element extends UpdateListener {
  constructor({ root, ...restProps }) {
    super(restProps);

    this[HIDDEN_PROPS_KEY] = {};
    this[HIDDEN_PROPS_KEY].root = root;
    this[HIDDEN_PROPS_KEY].content = null;
  }

  update() {
    const content = this.render();

    if (this[HIDDEN_PROPS_KEY].content) {
      this[HIDDEN_PROPS_KEY].root.replaceChild(content, this[HIDDEN_PROPS_KEY].content);
    } else {
      this[HIDDEN_PROPS_KEY].root.appendChild(content);
    }

    this[HIDDEN_PROPS_KEY].content = content;
  }

  // eslint-disable-next-line class-methods-use-this
  render() {
    throw new Error('Not implemented');
  }
}
