import createDomElement from '../../../utils/dom/createDomElement';

import Element from '../../../common/models/element';

import { getWeatherLoadingState } from '../../state/weather';
import { getActiveDictionary } from '../../state/languages';
import { getGeoDataObject } from '../../state/geodata';
import { getHoursMinutesString } from '../../utils/date';

import './date-time.css';

export default class DateTime extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.intervalId = null;
    this.curTime = null;
  }

  extractPropsFromState(state) {
    return {
      isLoading: getWeatherLoadingState(state),
      dictionary: getActiveDictionary(state),
      geoDataObject: getGeoDataObject(state),
    };
  }

  render() {
    const {
      isLoading,
      dictionary,
      geoDataObject,
    } = this.props;

    if (isLoading) {
      const loadingElement = createDomElement('span');
      loadingElement.innerText = dictionary['weather.loading'];
      return loadingElement;
    }

    if (geoDataObject.cityName !== '') {
      const dateTimeElement = this.renderDateTimeElement();
      return dateTimeElement;
    }

    const noDataElement = createDomElement('span');
    noDataElement.innerText = dictionary['weather.noData'];
    return noDataElement;
  }

  onStateUpdate() {
    if (!this.curTime) return;
    clearInterval(this.intervalId);
    this.intervalId = setInterval(() => {
      const curTimeArray = this.curTime.innerText.split(':');
      let intervalMinutes = +(curTimeArray[1]) + 1;
      let intervalHours = +(curTimeArray[0]);
      if (intervalMinutes === 60) {
        intervalMinutes = 0;
        intervalHours += 1;
      }
      const adjustedIntervalMinutes = getHoursMinutesString(intervalMinutes);

      let adjustedIntervalHours = intervalHours === 24 ? 0 : intervalHours;

      adjustedIntervalHours = getHoursMinutesString(intervalHours);

      this.curTime.innerText = `${adjustedIntervalHours}:${adjustedIntervalMinutes}`;
    }, 60000);
  }

  renderDateTimeElement() {
    const {
      dictionary,
      geoDataObject,
    } = this.props;
    const currentDate = geoDataObject.curDate;

    const dateTime = createDomElement('div', ['date-time']);
    const position = createDomElement('div', ['date-time__position']);
    position.innerHTML = geoDataObject.cityNameFull;
    const curDate = createDomElement('div', ['date-time__date']);
    const monthName = dictionary.months[currentDate.getMonth()];
    const dayName = dictionary.weekdays[currentDate.getDay()];
    curDate.innerHTML = `${dayName} ${currentDate.getUTCDate()} ${monthName}`;
    this.curTime = createDomElement('div', ['date-time__time']);
    this.curTime.innerHTML = `${getHoursMinutesString(currentDate.getUTCHours())}:${getHoursMinutesString(currentDate.getUTCMinutes())}`;

    dateTime.appendChild(position);
    dateTime.appendChild(curDate);
    dateTime.appendChild(this.curTime);

    return dateTime;
  }
}
