/* eslint-disable import/no-named-as-default */
/* eslint-disable import/no-named-as-default-member */
import Observable from './utils/Observable';

import INITIAL_STATE from './workspace/constants';

import Weather from './workspace/components/weather';
import Languages from './workspace/components/languages';
import SearchBar from './workspace/components/search-bar';
import WeatherLoader from './workspace/weather-loader';
import GeoDataLoader from './workspace/geodata-loader';
import TemperatureScales from './workspace/components/temp-scales';
import Map from './workspace/components/map';
import DateTime from './workspace/components/date-time';
import BackgroundRefreshButton from './workspace/components/background-refresh-button';
import BackgroundLoader from './workspace/background-loader';
import SpeechRecognitionButton from './workspace/components/speech-recognition-button/SpeechRecognitionButton';

import loadWeather from './workspace/utils/loadWeather';
import loadGeoData from './workspace/utils/loadGeodata';
import fetchBackgroundImage from './workspace/utils/fetchBackgroundImage';

import './assets/css/reset.css';
import './assets/css/styles.css';

window.addEventListener('load', () => {
  const appStateObservable = new Observable();

  const root = document.getElementById('root');

  const { notify } = appStateObservable;

  appStateObservable.subscribe(new BackgroundRefreshButton({ root, notify }));
  appStateObservable.subscribe(new Languages({ root, notify }));
  appStateObservable.subscribe(new TemperatureScales({ root, notify }));
  appStateObservable.subscribe(new SearchBar({ root, notify }));
  appStateObservable.subscribe(new SpeechRecognitionButton({ root, notify }));
  appStateObservable.subscribe(new DateTime({ root, notify }));
  appStateObservable.subscribe(new Weather({ root, notify }));
  appStateObservable.subscribe(new Map({ root, notify }));
  appStateObservable.subscribe(new WeatherLoader({ notify, loadWeather }));
  appStateObservable.subscribe(new GeoDataLoader({ notify, loadGeoData }));
  appStateObservable
    .subscribe(new BackgroundLoader({ notify, loadBackground: fetchBackgroundImage }));

  const config = INITIAL_STATE;
  const activeScale = JSON.parse(localStorage.getItem('activeScale'));
  if (activeScale) {
    config.scales.activeTempScale = activeScale;
    localStorage.removeItem('activeScale');
  }
  const activeDictionary = JSON.parse(localStorage.getItem('activeDictionary'));
  if (activeDictionary) {
    config.dictionaries.activeDictionary = activeDictionary;
    localStorage.removeItem('activeDictionary');
  }

  appStateObservable.notify(config);
});
