import UpdateListener from '../../common/models/update-listener';

import { setWeatherLoadingState, setWeatherObject } from '../state/weather';
import { getGeoDataObject } from '../state/geodata';
import { getActiveDictionary } from '../state/languages';
import { getActiveTempScale } from '../state/tempScales';


export default class WeatherLoader extends UpdateListener {
  constructor({ loadWeather, ...restProps }) {
    super(restProps);

    this.loadWeather = loadWeather;
  }

  // eslint-disable-next-line class-methods-use-this
  extractPropsFromState(state) {
    return {
      currentLang: getActiveDictionary(state),
      currentGeoData: getGeoDataObject(state),
      currentTempScale: getActiveTempScale(state),
    };
  }

  async onStateUpdate({
    currentGeoData: prevCurrentGeoData,
    currentTempScale: prevCurrentTempScale,
    currentLang: prevCurrentLang,
  }) {
    const { currentLang, currentGeoData, currentTempScale } = this.props;

    if ((!prevCurrentGeoData || currentGeoData.cityName !== prevCurrentGeoData.cityName
    || currentTempScale.code !== prevCurrentTempScale.code
    || currentLang['locale.name'] !== prevCurrentLang['locale.name'])
    && currentGeoData.lat !== '' && currentGeoData.lng !== '') {
      this.updateState(setWeatherLoadingState(true));

      this.loadWeather(currentGeoData, currentLang['locale.name'], currentTempScale)
        .then((responseData) => this.updateState(setWeatherObject(responseData)))
        .finally(() => this.updateState(setWeatherLoadingState(false)));
    }
  }
}
