/* eslint-disable no-undef */
/* eslint-disable no-unused-vars */
import createDomElement from '../../../utils/dom/createDomElement';

import Element from '../../../common/models/element';

import { getGeoDataLoadingState, getGeoDataObject } from '../../state/geodata';
import { getActiveDictionary } from '../../state/languages';
import API_KEYS from '../../api-keys';

import './map.css';
import getCoordsString from '../../utils/getCoordsString';

export default class Map extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.mapContainer = null;
  }

  extractPropsFromState(state) {
    return {
      isLoading: getGeoDataLoadingState(state),
      geoDataObject: getGeoDataObject(state),
      dictionary: getActiveDictionary(state),
    };
  }

  onStateUpdate() {
    const mapContainer = document.querySelector('#map');
    if (!mapContainer) return;
    const { geoDataObject } = this.props;
    const { mapbox } = API_KEYS;
    // eslint-disable-next-line no-undef
    mapboxgl.accessToken = mapbox;
    const map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/mapbox/streets-v11',
      center: [geoDataObject.lng, geoDataObject.lat],
      zoom: 9,
    });
  }

  render() {
    const { isLoading, geoDataObject, dictionary } = this.props;

    if (isLoading) {
      const loadingElement = createDomElement('span');
      loadingElement.innerText = dictionary['map.loading'];
      return loadingElement;
    }

    if (geoDataObject && geoDataObject.lat !== '' && geoDataObject.lng !== '') {
      const mapElement = createDomElement('div', ['map']);
      const longitude = createDomElement('div', ['map__longitude']);
      const latitude = createDomElement('div', ['map__latitude']);
      const lng = getCoordsString(geoDataObject.lng);
      const lat = getCoordsString(geoDataObject.lat);
      longitude.innerText = `${dictionary.longitude}: ${lng}`;
      latitude.innerText = `${dictionary.latitude}: ${lat}`;
      this.mapContainer = createDomElement('div');
      this.mapContainer.id = 'map';
      mapElement.appendChild(this.mapContainer);
      mapElement.appendChild(latitude);
      mapElement.appendChild(longitude);

      return mapElement;
    }

    const noDataElement = createDomElement('span');
    noDataElement.innerText = dictionary['map.noData'];
    return noDataElement;
  }
}
