export const getWeatherState = ({ weather }) => weather;

export const setWeatherState = (weather) => (state) => ({ ...state, weather });

export const getWeatherLoadingState = (state) => getWeatherState(state).isLoading;

export const setWeatherLoadingState = (isLoading) => (state) => setWeatherState({
  ...getWeatherState(state), isLoading,
})(state);

export const getWeatherObject = (state) => getWeatherState(state).weatherObject;

export const setWeatherObject = (weatherObject) => (state) => setWeatherState({
  ...getWeatherState(state), weatherObject,
})(state);
