export const getGeoDataState = ({ geoData }) => geoData;

export const setGeoDataState = (geoData) => (state) => ({ ...state, geoData });

export const getGeoDataLoadingState = (state) => getGeoDataState(state).isLoading;

export const setGeoDataLoadingState = (isLoading) => (state) => setGeoDataState({
  ...getGeoDataState(state), isLoading,
})(state);

export const getGeoDataObject = (state) => getGeoDataState(state).currentGeoData;

export const setGeoDataObject = (currentGeoData) => (state) => setGeoDataState({
  ...getGeoDataState(state), currentGeoData,
})(state);
