export default class Observable {
  constructor() {
    this.subscribers = [];
  }

  subscribe = (subscriber) => {
    const { subscribers } = this;

    subscribers.push(subscriber);

    const unsubscribe = () => {
      const subscriberIndex = subscribers.indexOf(subscriber);
      this.subscribers = subscribers.slice(0, subscriberIndex)
        .concat(subscribers.slice(subscriberIndex + 1, subscribers.length));
    };

    return unsubscribe;
  }

  notify = (value) => {
    this.subscribers.forEach(subscriber => subscriber.notify(value));
  }
}
