import createDomElement from '../../../utils/dom/createDomElement';

import Element from '../../../common/models/element';

import { getWeatherLoadingState, getWeatherObject } from '../../state/weather';
import { getActiveDictionary } from '../../state/languages';
import { getGeoDataObject } from '../../state/geodata';
import { getActiveTempScale } from '../../state/tempScales';

import renderWeatherBlock from '../../utils/renderWeatherBlock';

import './weather.css';

const Skycons = require('../../../../node_modules/skycons/skycons')(window);

export default class Weather extends Element {
  // constructor({ ...restProps }) {
  //   super(restProps);
  //   this.onStateUpdate = this.onStateUpdate.bind(this);
  // }

  extractPropsFromState(state) {
    return {
      isLoading: getWeatherLoadingState(state),
      weather: getWeatherObject(state),
      dictionary: getActiveDictionary(state),
      geoDataObject: getGeoDataObject(state),
      tempScale: getActiveTempScale(state),
    };
  }

  onStateUpdate() {
    const icons = document.querySelectorAll('.icon');
    icons.forEach((element) => {
      const skyConSelector = element.classList[2].split('-').join('_').toUpperCase();
      const skyCon = new Skycons({ color: '#ffffff' });
      skyCon.add(element.id, Skycons[skyConSelector]);
      skyCon.play();
    });
  }


  render() {
    const {
      isLoading,
      dictionary,
      weather,
      geoDataObject,
      tempScale,
    } = this.props;

    if (isLoading) {
      const loadingElement = createDomElement('span');
      loadingElement.innerText = dictionary['weather.loading'];
      return loadingElement;
    }

    if (weather.currently) {
      const weatherElement = renderWeatherBlock(weather, dictionary, geoDataObject, tempScale);
      return weatherElement;
    }

    const noDataElement = createDomElement('span');
    noDataElement.innerText = dictionary['weather.noData'];
    return noDataElement;
  }
}
