import renderList, { renderListItem } from '../../../common/components/list';
import renderButton from '../../../common/components/button';

import Element from '../../../common/models/element';

import lookupDataAttribute from '../../../utils/dom/lookupDataAttribute';

import { getTempScalesList, getActiveTempScale, setActiveTempScale } from '../../state/tempScales';

import SCALES_ID_DATA_ATTRIBUTE from './constants';

export default class TemperatureScales extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.onScaleItemClick = this.onScaleItemClick.bind(this);
  }

  extractPropsFromState(state) {
    return {
      activeTempScale: getActiveTempScale(state),
      tempScalesList: getTempScalesList(state),
    };
  }

  onScaleItemClick({ target, currentTarget }) {
    const scaleTitle = lookupDataAttribute(SCALES_ID_DATA_ATTRIBUTE, target, currentTarget);

    const { activeTempScale, tempScalesList } = this.props;

    const scale = scaleTitle ? tempScalesList.find(({ title }) => title === scaleTitle).code : null;

    if (scale && activeTempScale !== scale) {
      this.updateState(setActiveTempScale(scale));
      localStorage.setItem('activeScale', JSON.stringify(scale));
    }
  }

  render() {
    const { activeTempScale, tempScalesList } = this.props;

    const tempScalesListElement = renderList({
      items: tempScalesList,
      listItemRenderer: ({ item: { title, code }, ...restProps }) => {
        const scaleItem = renderListItem({
          item: renderButton({ caption: title }),
          isActive: activeTempScale === code,
          ...restProps,
        });
        scaleItem.dataset[SCALES_ID_DATA_ATTRIBUTE] = title;

        return scaleItem;
      },
      classNames: ['scales'],
    });

    tempScalesListElement.addEventListener('click', this.onScaleItemClick);

    return tempScalesListElement;
  }
}
