import renderInputForm from '../../../common/components/input-form';

import Element from '../../../common/models/element';

import { getActiveDictionary } from '../../state/languages';
import { getGeoDataObject, setGeoDataObject } from '../../state/geodata';

export default class SearchBar extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.onSearchBarSubmit = this.onSearchBarSubmit.bind(this);
  }

  extractPropsFromState(state) {
    return {
      activeDictionary: getActiveDictionary(state),
      currentGeoDataObject: getGeoDataObject(state),
    };
  }

  onSearchBarSubmit(e) {
    const { currentGeoDataObject } = this.props;
    const geoDataCopy = { ...currentGeoDataObject };
    geoDataCopy.cityName = e.target[0].value;
    if (geoDataCopy.cityName) {
      this.updateState(setGeoDataObject(geoDataCopy));
    }
    e.preventDefault();
  }

  render() {
    const { activeDictionary } = this.props;
    const buttonText = activeDictionary['search.buttonText'];

    const searchBarElement = renderInputForm({ caption: buttonText });

    searchBarElement.addEventListener('submit', this.onSearchBarSubmit);

    return searchBarElement;
  }
}
