import { getTimeOfDay, getSeasonOfTheYear } from './date';
import API_KEYS from '../api-keys';

export default async function loadGeodata(city, lang) {
  const { ipInfoToken, openCage } = API_KEYS;
  let cityToSearch = city;

  if (cityToSearch === '') {
    cityToSearch = (await fetch(`https://ipinfo.io/?token=${ipInfoToken}`)
      .then((response) => response.json())).city;
  }

  const openCageLink = `https://api.opencagedata.com/geocode/v1/json?q=${cityToSearch}&key=${openCage}&limit=1&language=${lang.toLowerCase()}`;

  const openCageResponce = await fetch(openCageLink).then((response) => (response.ok
    ? response.json() : Promise.reject(response)));

  const {
    geometry, annotations, components,
  } = openCageResponce.results[0];
  const { lat, lng } = geometry;
  const timeZone = annotations.timezone.name;
  const offsetMilliseconds = annotations.timezone.offset_sec * 1000;
  const curDate = new Date(Date.parse(openCageResponce.timestamp.created_http)
   + offsetMilliseconds);
  const season = getSeasonOfTheYear(curDate);
  const dayTime = getTimeOfDay(curDate);
  // eslint-disable-next-line no-underscore-dangle
  let cityName = components[components._type];
  const cityNameFull = `${cityName}, ${components.country}`;
  cityName = cityName.toLowerCase();
  const geoData = {
    timeZone,
    lat,
    lng,
    cityName,
    cityNameFull,
    season,
    dayTime,
    curDate,
  };

  return geoData;
}
