import renderList, { renderListItem } from '../../../common/components/list';
import renderButton from '../../../common/components/button';

import Element from '../../../common/models/element';

import lookupDataAttribute from '../../../utils/dom/lookupDataAttribute';

import { getDictionariesList, getActiveDictionary, setActiveDictionary } from '../../state/languages';

import { LANGUAGE_ID_DATA_ATTRIBUTE } from './constants';


import './languages.css';


export default class Languages extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.onLanguageItemClick = this.onLanguageItemClick.bind(this);
  }

  extractPropsFromState(state) {
    return {
      activeDictionary: getActiveDictionary(state),
      dictionariesList: getDictionariesList(state),
    };
  }

  onLanguageItemClick({ target, currentTarget }) {
    const languageTitle = lookupDataAttribute(LANGUAGE_ID_DATA_ATTRIBUTE, target, currentTarget);

    const { activeDictionary, dictionariesList } = this.props;

    const dictionary = languageTitle
      ? dictionariesList.find(({ title }) => title === languageTitle).bundle : null;

    if (dictionary && activeDictionary !== dictionary) {
      this.updateState(setActiveDictionary(dictionary));
      localStorage.setItem('activeDictionary', JSON.stringify(dictionary));
    }
  }

  render() {
    const { activeDictionary, dictionariesList } = this.props;

    const dictionariesListElement = renderList({
      items: dictionariesList,
      listItemRenderer: ({ item: { title, bundle }, ...restProps }) => {
        const dictionaryItem = renderListItem({
          item: renderButton({ caption: title }),
          isActive: activeDictionary === bundle,
          ...restProps,
        });
        dictionaryItem.dataset[LANGUAGE_ID_DATA_ATTRIBUTE] = title;

        return dictionaryItem;
      },
      classNames: ['languages'],
    });

    dictionariesListElement.addEventListener('click', this.onLanguageItemClick);

    return dictionariesListElement;
  }
}
