import createDomElement from '../../utils/dom/createDomElement';

function createSmallWeatherBlock(weather, day, elemNumber) {
  const {
    temperatureMax,
    temperatureMin,
    icon,
  } = weather[elemNumber];

  const smallBlock = createDomElement('div', ['day']);
  const dayName = createDomElement('div', ['day__name']);
  dayName.innerHTML = day;
  const temp = createDomElement('div', ['day__temp']);
  temp.innerHTML = Math.floor((temperatureMax + temperatureMin) / 2);

  const smallIcon = createDomElement('canvas', ['day__icon', 'icon', icon]);
  smallIcon.id = `day${elemNumber}`;

  smallBlock.appendChild(dayName);
  smallBlock.appendChild(temp);
  smallBlock.appendChild(smallIcon);
  return smallBlock;
}

export default function renderWeatherBlock(weather, dictionary, geoData, tempScale) {
  const { currently } = weather;
  const {
    temperature,
    icon,
    summary,
    apparentTemperature,
    windSpeed,
    humidity,
  } = currently;

  const roundedTemperature = Math.floor(temperature);
  const roundedApparentTemperature = Math.floor(apparentTemperature);

  const weatherElement = createDomElement('div', ['weather']);

  const weatherBox = createDomElement('div', ['weather-box']);
  const weatherBoxTemperature = createDomElement('div', ['weather-box__temperature']);
  const weatherBoxTemperatureCentered = createDomElement('div', ['weather-box__temperature_centered']);

  weatherBox.appendChild(weatherBoxTemperature);
  weatherBoxTemperature.appendChild(weatherBoxTemperatureCentered);

  weatherBoxTemperatureCentered.innerHTML = `${roundedTemperature} ${tempScale.literal}`;

  const summaryElement = createDomElement('div', ['weather-box__summary']);
  summaryElement.innerHTML = summary;

  const iconElement = createDomElement('canvas', ['weather-box__current-icon', 'icon', icon]);
  iconElement.id = 'current-icon';

  const feelsLike = createDomElement('div', ['weather-box__feels-like']);
  feelsLike.innerHTML = `${dictionary.feelsLike}: ${roundedApparentTemperature}`;

  const windElement = createDomElement('div', ['weather-box__wind']);
  windElement.innerHTML = `${dictionary.wind}: ${windSpeed}`;

  const humidityElement = createDomElement('div', ['weather-box__humidity']);
  humidityElement.innerHTML = `${dictionary.humidity}: ${humidity * 100}`;

  weatherBox.appendChild(iconElement);
  weatherBox.appendChild(summaryElement);
  weatherBox.appendChild(feelsLike);
  weatherBox.appendChild(windElement);
  weatherBox.appendChild(humidityElement);

  const threeDays = createDomElement('div', ['weather__three-days']);
  const d1 = new Date(new Date(geoData.curDate).setDate(geoData.curDate.getDate() + 1));
  const d2 = new Date(new Date(geoData.curDate).setDate(geoData.curDate.getDate() + 2));
  const d3 = new Date(new Date(geoData.curDate).setDate(geoData.curDate.getDate() + 3));
  const day1 = createSmallWeatherBlock(weather.daily.data,
    dictionary.weekdays[d1.getDay()], 1);
  const day2 = createSmallWeatherBlock(weather.daily.data,
    dictionary.weekdays[d2.getDay()], 2);
  const day3 = createSmallWeatherBlock(weather.daily.data,
    dictionary.weekdays[d3.getDay()], 3);

  threeDays.appendChild(day1);
  threeDays.appendChild(day2);
  threeDays.appendChild(day3);

  weatherElement.appendChild(weatherBox);
  weatherElement.appendChild(threeDays);

  return weatherElement;
}
