export const getBackgroundState = ({ background }) => background;

export const setBackgroundState = (background) => (state) => ({ ...state, background });

export const getBackgroundLoadingState = (state) => getBackgroundState(state).isLoading;

export const setBackgroundLoadingState = (isLoading) => (state) => setBackgroundState({
  ...getBackgroundState(state), isLoading,
})(state);

export const getBackgroundImage = (state) => getBackgroundState(state).backgroundSrc;

export const setBackgroundImage = (backgroundSrc) => (state) => setBackgroundState({
  ...getBackgroundState(state), backgroundSrc,
})(state);
