/* eslint-disable no-unused-vars */
/* eslint-disable class-methods-use-this */
import shallowEqual from '../../../utils/fp/shallowEqual';

const HIDDEN_PROPS_KEY = Symbol('hidden-props-key');

export default class UpdateListener {
  constructor(props) {
    const { notify, ...restProps } = props;
    this.props = restProps;
    this[HIDDEN_PROPS_KEY] = {};
    this[HIDDEN_PROPS_KEY].notify = notify;
    this[HIDDEN_PROPS_KEY].prevState = {};
  }

  notify(state) {
    const extractedProps = this.extractPropsFromState(state);
    this[HIDDEN_PROPS_KEY].prevState = state;

    const shouldUpdate = this.shouldUpdate(extractedProps);

    if (shouldUpdate) {
      const prevProps = this.props;
      this.props = extractedProps;

      Promise.resolve().then(() => this.onStateUpdate(prevProps));

      this.update();
    }
  }

  onStateUpdate(prevProps) {

  }

  update() {

  }

  shouldUpdate(props) {
    return !shallowEqual(this.props, props);
  }

  extractPropsFromState(state) {
    return state;
  }

  updateState(updater) {
    this[HIDDEN_PROPS_KEY].notify(
      updater(this[HIDDEN_PROPS_KEY].prevState),
    );
  }
}
