import UpdateListener from '../../common/models/update-listener';

import { setBackgroundImage, setBackgroundLoadingState } from '../state/background';
import { getGeoDataObject } from '../state/geodata';
import { getWeatherObject } from '../state/weather';

export default class BackgroundLoader extends UpdateListener {
  constructor({ loadBackground, ...restProps }) {
    super(restProps);

    this.loadBackground = loadBackground;
  }

  extractPropsFromState(state) {
    return {
      currentGeoData: getGeoDataObject(state),
      currentWeather: getWeatherObject(state),
    };
  }

  async onStateUpdate({ currentGeoData: prevCurrentGeoData }) {
    const { currentGeoData, currentWeather } = this.props;

    if (((!prevCurrentGeoData) || (currentGeoData.cityName !== prevCurrentGeoData.cityName))
    && currentGeoData.cityName !== '') {
      this.updateState(setBackgroundLoadingState(true));

      this.loadBackground(currentGeoData, currentWeather)
        .then((backgroundSrc) => this.updateState(setBackgroundImage(backgroundSrc)))
        .finally(() => this.updateState(setBackgroundLoadingState(false)));
    }
  }
}
