import renderButton from '../../../common/components/button';

import Element from '../../../common/models/element';

import { getActiveDictionary } from '../../state/languages';
import { getGeoDataObject } from '../../state/geodata';
import { setBackgroundImage } from '../../state/background';
import fetchBackgroundImage from '../../utils/fetchBackgroundImage';
import { getWeatherObject } from '../../state/weather';

export default class BackgroundRefreshButton extends Element {
  constructor({ ...restProps }) {
    super(restProps);
    this.onBackgroundRefreshButtonClick = this.onBackgroundRefreshButtonClick.bind(this);
  }

  extractPropsFromState(state) {
    return {
      activeDictionary: getActiveDictionary(state),
      currentGeoDataObject: getGeoDataObject(state),
      currentWeather: getWeatherObject(state),
    };
  }

  onBackgroundRefreshButtonClick() {
    const { currentGeoDataObject, currentWeather } = this.props;
    fetchBackgroundImage(currentGeoDataObject, currentWeather)
      .then((backgroundSrc) => this.updateState(setBackgroundImage(backgroundSrc)));
  }

  render() {
    const { activeDictionary } = this.props;
    const buttonText = activeDictionary['refresh.buttonText'];

    const refreshButton = renderButton({ caption: buttonText, onClick: this.onBackgroundRefreshButtonClick, classNames: ['refresh-button'] });

    return refreshButton;
  }
}
